/*HTML文档常规设置*/
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
/*100 ÷ 16 × 100% = 625% 谷歌浏览器不兼容最小10px默认为12px，改成100px的相对字体大小可解决*/
html {
  font-size: 625%;
}
body {
  width: 100%;
  -webkit-text-size-adjust: none;
  color: #333;
  font-family: "microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  background: #fff;
  font-size: 0.14rem;
  line-height: 1.5;
}
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
